@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,400;1,600;1,700&display=swap);
:root {
    --lightBeige: rgb(250, 223, 181, .8);
  }

* {
    padding: 0;
    margin: 0;
    font-family: 'Nunito', sans-serif;
}







nav {
    display: -webkit-inline-flex;
    display: inline-flex;
}

nav ul li {
    display: inline-block;
    margin: 0 2vw 0 2vw;
}

a {
    text-decoration: none;
    color: darkgreen;
}

nav img {
    display: inline-block;
    height: 60px;
}

header {
    position: fixed;
    height: 7vh;
    top: 0;
    width: 100%;
    padding: .1rem;
    background-color: var(--lightBeige);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    z-index: 1;
}

header img {
    margin-left: 2rem;
    height: 6.5vh;
}

#home {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    top: 0;
    background: url("https://source.unsplash.com/kXQ3J7_2fpc/1600x900") center
    center/cover no-repeat fixed;
    background-size: 100%;
    transition: background-size 5s ease-in-out;
}

#home:hover {
    background-size: 105%;
}

#home .container {
    border-style: solid;
    border-width: 5px;
    border-color: var(--lightBeige);
    background-color: rgba(253, 248, 239, 0.9);
    padding: 10vh 16vh 10vh 16vh;
    border-radius: 5px;
    text-align: center;
}

#home h1 {
    font-size: 72px;
    font-weight: 700;
    text-align: center;
    padding-bottom: 1rem; 
}
#about {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    background: url("https://source.unsplash.com/KsIfRQkR5vM/1600x900") center
    center/cover no-repeat fixed;
}

#about .textContainer {
    width: 50%;
    line-height: 2;
    text-align: left;
    margin-left: 10%;
}

#about .aboutText {
    font-size: 24px;
}

#about .aboutSmallText {
    line-height: 4;
    font-style: italic;
}
.formContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

form {
  margin-top: 100px;
  width: 40%;
  margin-bottom: 5rem;
}

.form-control:focus {
  border-color: grey;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 5px #333;
}


form h1 {
  padding: 0 0 2rem 0;
}

.ingredientSection, .instructionSection {
  padding: 1rem 0 1rem 0;
}

.ingredientSection label.form-label, .instructionSection label.form-label {
  padding: .5rem 0 .5rem 0;
}

form .ingredientList,
form .instructionList {
  padding: 1rem 0 0 0;
}

form #showIngredients p,
form #showInstructions p {
  color: rgb(92, 92, 92);
  font-size: 16px;
}

.btn-outline-primary {
  color: darkgreen;
  border-color: darkgreen;
  border-width: 2px;
  display: block;
}

.btn-outline-primary:hover {
  background-color: darkgreen;
  border-color: darkgreen;
}

.btn-primary {
  background-color: darkgreen;
  border-color: darkgreen;
  margin-top: 3.5rem;
}

.btn-primary:hover {
  background-color: rgb(1, 73, 1);
}




.recipesList {
  margin: 2rem 0 1rem 4rem;
  display: -webkit-inline-flex;
  display: inline-flex;
}

.recipeCard {
  width: 20vw;
  background-color: white;
  border-radius: 3px;
  box-shadow: 0 0 5px #333;
}

.filteredRecipes {
  margin-bottom: 9vh;
}

.recipeCard h1 {
  font-size: 22px;
  padding: 0.7rem;
  font-weight: 700;
  color: black;
}

.recipeCard h2 {
  padding: 0.7rem 0.7rem 0 0.7rem;
  line-height: 1.5;
  font-size: 22px;
}

.recipeCard h4 {
  margin: 1rem 0 1rem 0;
  line-height: 1.5;
}

.cardText {
  padding: .5rem;
}

.item {
  padding: 0 0 0.5rem 1rem;
}

.recipeCard img {
  width: 100%;
}

.singleContainer {
  width: 100vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.singleRecipe {
  width: 50%;
  margin: 5rem;
}

.singleRecipe img {
  width: 400px;
}

.singleRecipe h5 {
  font-size: 20px;
  font-weight: normal;
}
.singleRecipe a:hover {
  text-decoration: none;
}

.loading {
  margin-top: 5rem;
}

.searchBox {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 5rem;
    margin: 5rem 0 2rem 4rem;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
}

.searchBox h1 {
    font-size: 32px;
    margin-right: 2rem;
}

.searchBox form.form-inline {
    margin-right: 2rem;
    padding: 0;
}

.searchBox a:hover {
    text-decoration: none;
}




footer {
    width: 100%;
    height: 7vh;
    background-color: var(--lightBeige);
    text-align: center;
    padding: 1rem 0 1rem 0;
    bottom: 0;
    position: fixed;
}
